
@font-face {
  font-family: 'gsans';
  src: url(/static/fonts/GoogleSans-Regular.b5c77a6.ttf) format('truetype'),
  url(/static/fonts/GoogleSans-Medium.8d57e40.ttf) format('truetype'),
  url(/static/fonts/GoogleSans-Bold.4457817.ttf) format('truetype');
  font-weight: normal;
  font-style: normal;
}
#app {
  font-family: 'gsans', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 0px;
}
.page-footer {
  background-color: #cc0000;
}

.dropdown-item:hover {
  background-color: #CC0000!important;
}
.nav-item .nav-link  {
  color: white!important;
}
.dropdown .dropdown-menu .dropdown-item:active, .dropdown .dropdown-menu .dropdown-item:hover{color: white !important;
}
.nav-item {
  border-radius: 20px;
  background-color: #CC0000;
  margin: 5px;
  /*box-shadow: 0 1px 2px 0 rgba(60,64,67,0.302), 0 1px 3px 1px rgba(60,64,67,0.149);*/
}






















































































































































































































.ripple {
  position: absolute;
  z-index: 10;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  opacity: 1;
  -webkit-transform: scale(0);
          transform: scale(0);
}

.ripple.is-reppling {
  -webkit-animation: rippleAnimate .6s linear;
          animation: rippleAnimate .6s linear;
}

.ripple-parent {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

@-webkit-keyframes rippleAnimate {
  100% {
      opacity: 0;
      -webkit-transform: scale(3);
              transform: scale(3);
  }
}

@keyframes rippleAnimate {
  100% {
      opacity: 0;
      -webkit-transform: scale(3);
              transform: scale(3);
  }
}













































































